import React, {useState} from "react";
import { Redirect } from 'react-router-dom';
import { useAuth } from "../context/Auth";
import { ApiUrl } from "../constants/ApiUrl";

export default function Login(props) {

  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isError, setIsError] = useState(false);
  const [password, setPassword] = useState("");
  const [username, setUsername] = useState("");
  const { setAuthTokens } = useAuth();

  function postLogin(event) {
    event.preventDefault();
    fetch( `${ApiUrl}/login`, {
      method: 'POST',
      headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
      },
      body: JSON.stringify({username: username, password: password})})
    .then(res => res.json())
    .then(isAuthenticated => {
      if(isAuthenticated){
        setAuthTokens(true);
        setIsLoggedIn(true);
      } else {
        setIsError(true);
      }
    })
    .catch(error => console.log("Error getting authentication: ", error));
  }

  if (isLoggedIn) {
    return <Redirect to="/" />;
  }

  return (
    <div className="login-card">
      <form className="login-form" action={`${ApiUrl}/login`} method='POST' >
        <input
          type="text"
          name="username"
          value={username}
          onChange={e => {
            setUsername(e.target.value);
          }}
          placeholder="Username"
        />
        <input
          type="password"
          name="password"
          value={password}
          onChange={e => {
            setPassword(e.target.value);
          }}
          placeholder="Password"
        />
        <button type="submit" onClick={postLogin} className="btn btn-success">Sign In</button>
      </form>
        { isError &&<div style={{backgroundColor: 'red'}}>The username or password provided were incorrect!</div> }
    </div>
  );
}