import React from 'react';
import {Navbar, Nav} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import {useAuth} from '../context/Auth'

export default function Header(){

    const { authTokens, setAuthTokens } = useAuth();

    function logout(){
        if(authTokens){
            setAuthTokens(false);
        }
    }

    return ( 
        <Navbar expand="md" className="navbar" bg="success" text-variant="warning" style={{padding: '0 3rem'}}>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav>
                <LinkContainer to={"/"} className="link">
                    <Nav.Link>Home</Nav.Link>
                </LinkContainer>
                <LinkContainer to={"/kataPengantar"} className="link">
                    <Nav.Link>Kata Pengantar</Nav.Link>
                </LinkContainer>
                {authTokens 
                    && (<LinkContainer to={'/edit/new'} className="link">
                        <Nav.Link>Create new plant entry</Nav.Link>
                        </LinkContainer>)}
                <LinkContainer to={'/login'} onClick={logout} className="link create-new">
                    <Nav.Link>{authTokens ? 'Logout' : 'Admin'}</Nav.Link>
                </LinkContainer>
            </Nav> 
            </Navbar.Collapse>   
        </Navbar>
        
    )
}